import React from 'react'
import logo from './logo.svg'
import './navbar.css'
import {
    Link
} from "react-router-dom";
const Navbar = () => {
    return (
        <>
            <div className='Navbar'>
                <nav className="navbar navbar-expand-lg">
                    <div className="container-fluid mx-4">
                        <a className="navbar-brand ms-5" href="/">
                            <img src={logo} alt='logo'></img></a>
                        <button
                            className="navbar-toggler"
                            type="button"
                            data-bs-toggle="collapse"
                            data-bs-target="#navbarSupportedContent"
                            aria-controls="navbarSupportedContent"
                            aria-expanded="false"
                            aria-label="Toggle navigation"
                        >
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        <div className="collapse navbar-collapse" id="navbarSupportedContent">
                            <ul className="navbar-nav mx-5 Font">
                                <li className="nav-item mx-3 ">
                                    <Link className="nav-link Font" to='/' >Home</Link>
                                </li>
                                <li className="nav-item mx-3">
                                    <Link className="nav-link Font" to='/login_dark'  >Features</Link>
                                </li>
                                <li className="nav-item mx-3">
                                    <Link className="nav-link Font" to='/ProfileDevelopment'  >Pricing</Link>
                                </li>
                                <li className="nav-item mx-3">
                                    <Link className="nav-link Font" to='/'  >FAQs</Link>
                                </li>
                                <li className="nav-item mx-3">
                                    <Link className="nav-link Font" to='/'  >About Us</Link>
                                </li>
                                <li className="nav-item mx-3">
                                    <Link className="nav-link Font" to='/'  >Contact us</Link>
                                </li>
                            </ul>
                            <button className='navbar_sign'><Link to="/login" >Sign In</Link></button>
                        </div>
                    </div>
                </nav>
            </div>
        </>
    )
}
export default Navbar





















